@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/Oswald-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/Oswald-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
section {
  outline: 1px dotted purple;
}

li {
  outline: 1px dotted red;
}

*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: top;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--default-white);
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

:root {
  --main-font: "Oswald", "Big Caslon", "sans-serif";
  --default-black: #000000;
  --black1: #444444;
  --black2: #111111;
  --black3: #222222;
  --grey1: #f2f2f2;
  --grey2: #eaeaea;
  --grey3: #cdcdcd;
  --grey4: #bababa;
  --grey5: #d9d9d9;
  --grey6: #e7e7e7;
  --default-white: #ffffff;
  --background-page: #e5e5e5;
  --green1: rgba(104, 183, 56, 0.85);
  --green2: #68b738;
  --arial: "Arial", "sans-serif", "Verdana";
  --tablet-width: 768px;
  --desktop-width: 1440px;
  --mobile-width: 320px;
  --mobile-padding: 20px;
  --tablet-padding: 30px;
  --tablet-form-l-padding: 70px;
  --tablet-form-r-padding: 65px;
  --desktop-padding: 110px;
}

.page .page__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  background-color: var(--default-white);
}
@media (min-width: 768px) {
  .page .page__wrapper {
    max-width: var(--tablet-width);
  }
}
@media (min-width: 1440px) {
  .page .page__wrapper {
    max-width: var(--desktop-width);
  }
}

.main-header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0 auto;
  width: 100%;
  flex-wrap: wrap;
  min-height: 65px;
  padding-top: 10px;
  color: var(--default-black);
  font-size: 18px;
  line-height: 1;
}
.main-header .main-header__logo-link {
  margin-left: var(--tablet-padding);
  margin-right: 56px;
}
.main-header .main-header__title-link {
  margin-right: auto;
}
@media (min-width: 768px) {
  .main-header {
    padding-top: 20px;
    padding-left: var(--tablet-padding);
    padding-right: var(--tablet-padding);
    min-height: 76px;
  }
  .main-header .main-header__logo-link {
    margin-left: 0;
    margin-right: 19px;
  }
  .main-header .main-header__title-link {
    margin-right: auto;
  }
  .main-header .header-black {
    color: var(--default-black);
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding-top: 53px;
    padding-left: var(--desktop-padding);
    padding-right: var(--desktop-padding);
    min-height: 76px;
  }
  .main-header .main-header__logo-link {
    margin-right: 14px;
  }
  .main-header .main-header__title-link {
    margin-right: auto;
  }
}

.main-nav {
  width: 100%;
  position: absolute;
  top: 65px;
  min-height: 65px;
}
.main-nav .main-nav__wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: var(--default-white);
}
.main-nav .main-nav__menu-opener {
  padding: 0;
  background: none;
  border: 0;
  position: absolute;
  top: -37px;
  right: 20px;
  z-index: 3;
  display: block;
  width: 24px;
  min-height: 16px;
  cursor: pointer;
  background-color: #ffffff;
}
.main-nav .main-nav__menu-opener:hover, .main-nav .main-nav__menu-opener:active {
  background-color: #624530;
}
.main-nav--closed .main-nav__menu-opener::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  box-shadow: 0 2px 0 0 #000000 inset, 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}
.main-nav--closed .main-nav__menu-opener:active::before {
  box-shadow: 0 2px 0 0 #ff0000 inset, 0 7px 0 0 #ff0000, 0 14px 0 0 #ff0000;
}
.main-nav--opened .main-nav__menu-opener::before, .main-nav--opened .main-nav__menu-opener::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__menu-opener::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__menu-opener::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__menu-opener:active::before, .main-nav--opened .main-nav__menu-opener:active::after {
  background-color: #ff0000;
}
.main-nav .main-nav__link {
  position: relative;
  z-index: 1;
  font-size: 20px;
  line-height: 30px;
  padding-top: 23px;
  padding-bottom: 23px;
  color: var(--default-black);
  text-transform: uppercase;
  text-align: center;
}
.main-nav .main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav .main-nav__link:active .main-nav__name {
  position: relative;
}
.main-nav .main-nav__link:active .main-nav__name::after {
  content: "";
  position: absolute;
  top: auto;
  left: 1.5px;
  right: 1.5px;
  bottom: -6px;
  height: 2px;
  background-color: var(--green2);
}
@media (min-width: 768px) {
  .main-nav {
    min-height: auto;
    position: relative;
    top: auto;
    width: auto;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav .main-nav__menu-opener {
    display: none;
  }
  .main-nav .main-nav__wrapper {
    flex-direction: row;
    align-items: center;
  }
  .main-nav .main-nav__link {
    padding: 0;
  }
  .main-nav .main-nav__link:active .main-nav__name::after {
    content: "";
    position: absolute;
    top: auto;
    left: 1.5px;
    right: 1.5px;
    bottom: -8px;
    height: 2px;
    background-color: var(--green2);
  }
  .main-nav .main-nav__link--active .main-nav__name::after {
    content: "";
    position: absolute;
    top: auto;
    left: 1.5px;
    right: 1.5px;
    bottom: -8px;
    height: 2px;
    background-color: var(--green2);
  }
  .main-nav .main-nav__index-link {
    margin-right: 33px;
  }
  .main-nav .main-nav__catalog-link {
    margin-right: 36px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    font-size: 20px;
    line-height: 30px;
    color: var(--default-white);
  }
  .main-nav .main-nav__index-link {
    margin-right: 35px;
  }
  .main-nav .main-nav__catalog-link {
    margin-right: 32px;
  }
  .main-nav .main-nav__link {
    padding: 0;
  }
  .main-nav .main-nav__link:active .main-nav__name::after {
    content: "";
    position: absolute;
    top: auto;
    left: 1.5px;
    right: 1.5px;
    bottom: -8px;
    height: 2px;
    background-color: var(--default-white);
  }
  .main-nav .main-nav__link--active .main-nav__name::after {
    content: "";
    position: absolute;
    top: auto;
    left: 1.5px;
    right: 1.5px;
    bottom: -8px;
    height: 2px;
    background-color: var(--default-white);
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__link {
    display: none;
  }

  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    min-height: 198px;
  }

  .main-nav--nojs .main-nav__menu-opener {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .main-nav__link {
    display: block;
  }

  .main-nav--nojs .main-nav__link--active {
    padding-left: 105px;
    padding-right: 105px;
    text-align: center;
  }

  .main-nav--nojs .user-list__item {
    display: block;
    background-color: #6a4d38;
  }
}
.main-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
}

